import { Vector2 } from 'three';
import { SAOPass } from 'three/examples/jsm/postprocessing/SAOPass.js';
import { watch } from 'vue';
import EffectPass from './EffectPass.js';

export default {
  extends: EffectPass,
  props: {
    depthTexture: { type: Boolean, default: false },
    useNormals: { type: Boolean, default: false },
    resolution: Object,
  },
  mounted() {
    if (!this.three.scene) {
      console.error('Missing Scene');
    }
    if (!this.three.camera) {
      console.error('Missing Camera');
    }

    const resolution = this.resolution ? new Vector2(this.resolution.x, this.resolution.y) : new Vector2(this.three.size.width, this.three.size.height);
    const pass = new SAOPass(this.three.scene, this.three.camera, this.depthTexture, this.useNormals, resolution);
    this.passes.push(pass);
    this.pass = pass;
  },
  __hmrId: 'SAOPass',
};
